@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

body{
    margin:0;
    padding:0;
    font-family: 'Poppins', sans-serif;
    background-color: white;
}
.container{
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.navbar {
    height: 60px;
    background-color:white;
    position: relative;
    
}

.menu-icon {
    display: none;
}

.nav-elements ul {
    display: flex;
    justify-content: space-between;
    list-style-type: none;
}
.nav-elements ul li:not(:last-child) {
    margin-right: 60px;
}
.nav-elements ul a {
    font-size: 16px;
    font-weight: 400;
    color: #2f234f;
    text-decoration: none;
}
.nav-elements ul a:hover{
  color:red;
}

@media (max-width: 768px) {
    .nav-elements ul li:not(:last-child) {
      margin-right: 30px;
    }
}
@media (max-width: 600px) {
    .menu-icon {
      display: block;
      cursor: pointer;
    }
    .nav-elements {
        position: absolute;
        right: 0;
        top: 60px;
        background-color: white;
        width: 0px;
        height: calc(100vh - 60px);
        transition: all 0.3s ease-in;
        overflow: hidden;
      }
      .nav-elements.active {
        width: 270px;
      }
       .nav-elements ul {
    display: flex;
    flex-direction: column;
  }

  .nav-elements ul li {
    margin-right: unset;
    margin-top: 22px;
  }
}
